/* VARIABLES */
$blue : #00ABC8;
$white : #FFF;

/* DOM */
html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 150px;
}

/* Header */
.header-section {
    padding: 25px 0;
    background-color: $white ;
    width: 100%;
    height: 10%;
}

.navbar-brand {
    float: left;
    padding: 0;
}

.navbar-brand img {
    float: left;
    transition: all .5s ease-out;
    margin: 0;
    width: 25%;
}

.header input {
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .12), 0 1px 2px 0 rgba(0, 0, 0, .24);
}

 /* FOOTER */
.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  /*height: 10%;*/
  background-color: $white ;
  text-align: center;
  border-top: 5px solid $blue;
}

.container {
  margin-top: 10px;
  color: $white;
  width: 100%;
}

.fa-github {
    color: #4099FF;
}

.fa-facebook {
    color: #3B5998;
}

.fa-linkedin {
    color: #007bb6;
}

.fa {
    opacity: 0.7;
    transition: 1s;
    -webkit-transition: 1s;
}

.fa:hover {
    opacity: 1;
    transition: 1s;
    -webkit-transition: 1s;
}
